/**
 * LRUï¼ˆæœ€è¿‘æœ€å°‘ä½¿ç”¨ï¼‰ç¼“å­˜å®ç°
 * ç”¨äºç¼“å­˜è§£å¯†ç»“æœï¼Œé¿å…é‡å¤è§£å¯†æ“ä½œ
 */

export class LRUCache<K, V> {
  private maxSize: number;
  private cache: Map<K, V>;

  constructor(maxSize: number = 200) {
    this.maxSize = maxSize;
    this.cache = new Map();
  }

  /**
   * è·å–ç¼“å­˜å€¼
   * @param key ç¼“å­˜é”®
   * @returns ç¼“å­˜å€¼ï¼Œå¦‚æœä¸å­˜åœ¨åˆ™è¿”å› undefined
   */
  get(key: K): V | undefined {
    if (!this.cache.has(key)) {
      return undefined;
    }

    // LRU ç­–ç•¥ï¼šå°†è®¿é—®çš„é¡¹ç§»åˆ°æœ€åï¼ˆæœ€æ–°ï¼‰
    const value = this.cache.get(key)!;
    this.cache.delete(key);
    this.cache.set(key, value);

    return value;
  }

  /**
   * è®¾ç½®ç¼“å­˜å€¼
   * @param key ç¼“å­˜é”®
   * @param value ç¼“å­˜å€¼
   */
  set(key: K, value: V): void {
    // å¦‚æœå·²å­˜åœ¨ï¼Œå…ˆåˆ é™¤ï¼ˆä¸ºäº†æ›´æ–°é¡ºåºï¼‰
    if (this.cache.has(key)) {
      this.cache.delete(key);
    }

    // å¦‚æœç¼“å­˜å·²æ»¡ï¼Œåˆ é™¤æœ€æ—§çš„é¡¹ï¼ˆç¬¬ä¸€ä¸ªï¼‰
    if (this.cache.size >= this.maxSize) {
      const firstKey = this.cache.keys().next().value;
      if (firstKey !== undefined) {
        this.cache.delete(firstKey);
      }
    }

    // æ·»åŠ æ–°é¡¹åˆ°æœ«å°¾ï¼ˆæœ€æ–°ï¼‰
    this.cache.set(key, value);
  }

  /**
   * æ£€æŸ¥ç¼“å­˜ä¸­æ˜¯å¦å­˜åœ¨æŒ‡å®šé”®
   * @param key ç¼“å­˜é”®
   * @returns æ˜¯å¦å­˜åœ¨
   */
  has(key: K): boolean {
    return this.cache.has(key);
  }

  /**
   * åˆ é™¤ç¼“å­˜é¡¹
   * @param key ç¼“å­˜é”®
   */
  delete(key: K): void {
    this.cache.delete(key);
  }

  /**
   * æ¸…ç©ºæ‰€æœ‰ç¼“å­˜
   */
  clear(): void {
    this.cache.clear();
  }

  /**
   * è·å–å½“å‰ç¼“å­˜å¤§å°
   * @returns ç¼“å­˜é¡¹æ•°é‡
   */
  size(): number {
    return this.cache.size;
  }

  /**
   * è·å–ç¼“å­˜å®¹é‡
   * @returns æœ€å¤§ç¼“å­˜é¡¹æ•°é‡
   */
  capacity(): number {
    return this.maxSize;
  }
}
