// è¡Œå·®å¼‚ç±»å‹
interface DiffLine {
  lineNumber: number;
  content: string;
  type: 'same' | 'modified' | 'added' | 'removed';
}

// æ”¹è¿›çš„è¡Œçº§ diff ç®—æ³•ï¼ˆå‡å°‘è¯¯åˆ¤ï¼‰
function computeLineDiff(localText: string, serverText: string): { localLines: DiffLine[], serverLines: DiffLine[] } {
  const localLines = localText.split('\n');
  const serverLines = serverText.split('\n');

  // ä½¿ç”¨ LCSï¼ˆæœ€é•¿å…¬å…±å­åºåˆ—ï¼‰ç®—æ³•æ‰¾å‡ºçœŸæ­£ç›¸åŒçš„è¡Œ
  const lcs = findLCS(localLines, serverLines);
  const lcsSet = new Set(lcs.map((item) => `${item.localIndex}-${item.serverIndex}`));

  const localResult: DiffLine[] = [];
  const serverResult: DiffLine[] = [];

  let localIndex = 0;
  let serverIndex = 0;

  while (localIndex < localLines.length || serverIndex < serverLines.length) {
    const isLCS = lcsSet.has(`${localIndex}-${serverIndex}`);

    if (isLCS) {
      // ç›¸åŒè¡Œ
      localResult.push({ lineNumber: localIndex + 1, content: localLines[localIndex], type: 'same' });
      serverResult.push({ lineNumber: serverIndex + 1, content: serverLines[serverIndex], type: 'same' });
      localIndex++;
      serverIndex++;
    } else {
      // æ£€æŸ¥æ˜¯å¦æœ‰åç»­åŒ¹é…
      const hasLocalMatch = lcs.some((item) => item.localIndex === localIndex);
      const hasServerMatch = lcs.some((item) => item.serverIndex === serverIndex);

      if (!hasLocalMatch && localIndex < localLines.length) {
        // æœ¬åœ°è¡Œåœ¨æœåŠ¡å™¨ä¸­ä¸å­˜åœ¨ â†’ æ–°å¢è¡Œ
        localResult.push({ lineNumber: localIndex + 1, content: localLines[localIndex], type: 'added' });
        localIndex++;
      } else if (!hasServerMatch && serverIndex < serverLines.length) {
        // æœåŠ¡å™¨è¡Œåœ¨æœ¬åœ°ä¸­ä¸å­˜åœ¨ â†’ åˆ é™¤è¡Œ
        serverResult.push({ lineNumber: serverIndex + 1, content: serverLines[serverIndex], type: 'removed' });
        serverIndex++;
      } else {
        // ä¸¤è¾¹éƒ½æœ‰ä½†å†…å®¹ä¸åŒ â†’ ä¿®æ”¹è¡Œ
        if (localIndex < localLines.length && serverIndex < serverLines.length) {
          localResult.push({ lineNumber: localIndex + 1, content: localLines[localIndex], type: 'modified' });
          serverResult.push({ lineNumber: serverIndex + 1, content: serverLines[serverIndex], type: 'modified' });
          localIndex++;
          serverIndex++;
        } else if (localIndex < localLines.length) {
          localResult.push({ lineNumber: localIndex + 1, content: localLines[localIndex], type: 'added' });
          localIndex++;
        } else if (serverIndex < serverLines.length) {
          serverResult.push({ lineNumber: serverIndex + 1, content: serverLines[serverIndex], type: 'removed' });
          serverIndex++;
        }
      }
    }
  }

  return { localLines: localResult, serverLines: serverResult };
}

// ç®€åŒ–çš„ LCS ç®—æ³•ï¼ˆæœ€é•¿å…¬å…±å­åºåˆ—ï¼‰
function findLCS(arr1: string[], arr2: string[]): Array<{ localIndex: number; serverIndex: number }> {
  const m = arr1.length;
  const n = arr2.length;
  const dp: number[][] = Array(m + 1).fill(0).map(() => Array(n + 1).fill(0));

  // æ„å»º DP è¡¨
  for (let i = 1; i <= m; i++) {
    for (let j = 1; j <= n; j++) {
      if (arr1[i - 1] === arr2[j - 1]) {
        dp[i][j] = dp[i - 1][j - 1] + 1;
      } else {
        dp[i][j] = Math.max(dp[i - 1][j], dp[i][j - 1]);
      }
    }
  }

  // å›æº¯æ‰¾å‡º LCS çš„ç´¢å¼•å¯¹
  const result: Array<{ localIndex: number; serverIndex: number }> = [];
  let i = m, j = n;
  while (i > 0 && j > 0) {
    if (arr1[i - 1] === arr2[j - 1]) {
      result.unshift({ localIndex: i - 1, serverIndex: j - 1 });
      i--;
      j--;
    } else if (dp[i - 1][j] > dp[i][j - 1]) {
      i--;
    } else {
      j--;
    }
  }

  return result;
}

