  // ğŸ’¡ è¾…åŠ©å‡½æ•°ï¼šè§£å¯†å•æ¡å¯†ç ï¼ˆå¸¦ç¼“å­˜ï¼‰
  const decryptPassword = useCallback(async (pwd: Password): Promise<Password> => {
    const cacheKey = `${pwd.id}-${pwd.updated_at}`;
    const passwordDecryptCache = passwordDecryptCacheRef.current;

    // 1. æ£€æŸ¥ç¼“å­˜
    const cached = passwordDecryptCache.get(cacheKey);
    if (cached) {
      return {
        ...pwd,
        site: cached.site,
        username: cached.username,
        password: cached.password,
        notes: cached.notes,
      };
    }

    // 2. ç¼“å­˜æœªå‘½ä¸­ï¼Œæ‰§è¡Œè§£å¯†
    try {
      const decryptedJson = await crypto.decrypt(
        {
          ciphertext: pwd.encrypted_site,
          iv: pwd.iv,
          algorithm: 'AES-256-GCM',
        },
        false
      );

      try {
        // å°è¯•è§£æä¸º JSONï¼ˆæ–°æ ¼å¼ï¼‰
        const passwordData = JSON.parse(decryptedJson);

        const decryptedData = {
          site: String(passwordData.site || ''),
          username: String(passwordData.username || ''),
          password: String(passwordData.password || ''),
          notes: String(passwordData.notes || ''),
        };

        // 3. å­˜å…¥ç¼“å­˜
        passwordDecryptCache.set(cacheKey, decryptedData);

        return { ...pwd, ...decryptedData };
      } catch {
        // æ—§æ ¼å¼ï¼Œåˆ†åˆ«è§£å¯†å„å­—æ®µ
        const username = await crypto.decrypt(
          { ciphertext: pwd.encrypted_username, iv: pwd.iv, algorithm: 'AES-256-GCM' },
          false
        );
        const password = await crypto.decrypt(
          { ciphertext: pwd.encrypted_password, iv: pwd.iv, algorithm: 'AES-256-GCM' },
          false
        );

        let notes = '';
        if (pwd.encrypted_notes) {
          try {
            notes = await crypto.decrypt(
              { ciphertext: pwd.encrypted_notes, iv: pwd.iv, algorithm: 'AES-256-GCM' },
              false
            );
          } catch {}
        }

        const decryptedData = {
          site: decryptedJson,
          username,
          password,
          notes,
        };

        // 3. å­˜å…¥ç¼“å­˜
        passwordDecryptCache.set(cacheKey, decryptedData);

        return { ...pwd, ...decryptedData };
      }
    } catch (err) {
      console.error('[decryptPassword] è§£å¯†å¯†ç å¤±è´¥:', err);
      return {
        ...pwd,
        site: '[è§£å¯†å¤±è´¥]',
        username: '[è§£å¯†å¤±è´¥]',
        password: '[è§£å¯†å¤±è´¥]',
        notes: '',
      };
    }
  }, []);

  // åŠ è½½å¯†ç åˆ—è¡¨ï¼ˆæ··åˆåˆ†é¡µç­–ç•¥ï¼‰
