  // æ£€æµ‹æ˜¯å¦ä¸ºæœ‰æ•ˆçš„ URL
  const isValidUrl = (text: string): boolean => {
    try {
      const url = new URL(text);
      return url.protocol === 'http:' || url.protocol === 'https:';
    } catch {
      // å¦‚æœä¸æ˜¯å®Œæ•´ URLï¼Œå°è¯•æ£€æµ‹æ˜¯å¦æ˜¯åŸŸåæ ¼å¼
      const domainPattern = /^([a-zA-Z0-9]([a-zA-Z0-9-]*[a-zA-Z0-9])?\.)+[a-zA-Z]{2,}$/;
      return domainPattern.test(text);
    }
  };

  // æ ¼å¼åŒ– URLï¼ˆå¦‚æœæ˜¯åŸŸåæ ¼å¼ï¼Œæ·»åŠ  https://ï¼‰
  const formatUrl = (text: string): string => {
    try {
      new URL(text);
      return text;
    } catch {
      return `https://${text}`;
    }
  };

  // å¤„ç† Markdown é“¾æ¥ç‚¹å‡»ï¼šé”šç‚¹é“¾æ¥æ»šåŠ¨åˆ°æŒ‡å®šä½ç½®ï¼Œå¤–éƒ¨é“¾æ¥åœ¨æ–°æ ‡ç­¾é¡µæ‰“å¼€
  const handleMarkdownLinkClick = (e: React.MouseEvent<HTMLAnchorElement>, href?: string) => {
    if (!href) return;

    // å¦‚æœæ˜¯é”šç‚¹é“¾æ¥ï¼ˆä»¥ # å¼€å¤´ï¼‰
    if (href.startsWith('#')) {
      e.preventDefault();
      const targetId = href.substring(1); // ç§»é™¤ # å·
      const targetElement = document.getElementById(targetId);

      if (targetElement) {
        // å¹³æ»‘æ»šåŠ¨åˆ°ç›®æ ‡å…ƒç´ 
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
        });
      } else {
        // å¦‚æœæ‰¾ä¸åˆ°å…ƒç´ ï¼Œå°è¯•é€šè¿‡æ–‡æœ¬å†…å®¹æŸ¥æ‰¾æ ‡é¢˜
        const decodedId = decodeURIComponent(targetId);
        const headings = document.querySelectorAll('h1, h2, h3, h4, h5, h6');
        for (const heading of headings) {
          if (heading.textContent === decodedId || heading.id === decodedId) {
            heading.scrollIntoView({
              behavior: 'smooth',
              block: 'start',
            });
            break;
          }
        }
      }
    }
    // å¤–éƒ¨é“¾æ¥å·²é€šè¿‡ target="_blank" è‡ªåŠ¨åœ¨æ–°æ ‡ç­¾é¡µæ‰“å¼€
  };

