  // æ ¹æ®é‚®ç®±ç”Ÿæˆå¤´åƒé¢œè‰²ï¼ˆç¡®ä¿åŒä¸€é‚®ç®±é¢œè‰²ä¸€è‡´ï¼‰
  const getAvatarColor = (email: string): string => {
    const colors = [
      'bg-blue-500',
      'bg-blue-500',
      'bg-pink-500',
      'bg-red-500',
      'bg-orange-500',
      'bg-yellow-500',
      'bg-green-500',
      'bg-teal-500',
      'bg-cyan-500',
      'bg-indigo-500',
    ];

    // ç®€å•å“ˆå¸Œå‡½æ•°ï¼ŒåŸºäºé‚®ç®±å­—ç¬¦ä¸²ç”Ÿæˆç´¢å¼•
    let hash = 0;
    for (let i = 0; i < email.length; i++) {
      hash = email.charCodeAt(i) + ((hash << 5) - hash);
    }
    const index = Math.abs(hash) % colors.length;
    return colors[index];
  };

  // è·å–é‚®ç®±é¦–å­—æ¯
  const getAvatarInitial = (email: string): string => {
    return email.charAt(0).toUpperCase();
  };

  // é‚®ç®±åŠ å¯†æ˜¾ç¤ºï¼ˆä¾‹å¦‚ï¼štest@example.com â†’ t***@example.comï¼‰
  const maskEmail = (email: string): string => {
    if (!email || !email.includes('@')) return email;
    const [localPart, domain] = email.split('@');
    if (!localPart || localPart.length === 0) return email;

    // åªæ˜¾ç¤ºç¬¬ä¸€ä¸ªå­—ç¬¦ï¼Œå…¶ä½™ç”¨ *** æ›¿ä»£
    const maskedLocal = localPart.charAt(0) + '***';
    return `${maskedLocal}@${domain}`;
  };

  // ğŸ’¡ æ ¼å¼åŒ–ä¿å­˜æ—¶é—´ï¼ˆé™æ€ç»å¯¹æ—¶é—´ï¼Œé›¶æ€§èƒ½å¼€é”€ï¼‰
  const formatSaveTime = useCallback((timestamp: number): string => {
    const date = new Date(timestamp);
    const now = new Date();

    // ä»Šå¤©ï¼šæ˜¾ç¤º"ä»Šå¤© HH:mm"
    if (date.toDateString() === now.toDateString()) {
      return `${t.common.today} ${date.toLocaleTimeString(language === 'zh-CN' ? 'zh-CN' : 'en-US', {
        hour: '2-digit',
        minute: '2-digit'
      })}`;
    }

    // æ˜¨å¤©ï¼šæ˜¾ç¤º"æ˜¨å¤© HH:mm"
    const yesterday = new Date(now);
    yesterday.setDate(yesterday.getDate() - 1);
    if (date.toDateString() === yesterday.toDateString()) {
      return `${t.common.yesterday} ${date.toLocaleTimeString(language === 'zh-CN' ? 'zh-CN' : 'en-US', {
        hour: '2-digit',
        minute: '2-digit'
      })}`;
    }

    // å…¶ä»–ï¼šæ˜¾ç¤º"MM/DD HH:mm"
    return date.toLocaleString(language === 'zh-CN' ? 'zh-CN' : 'en-US', {
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    });
  }, [language, t]);

  // ä¼šè¯æ¢å¤ï¼šé‡æ–°è¾“å…¥ä¸»å¯†ç æ¢å¤åŠ å¯†å¯†é’¥
  const handleSessionRestore = async () => {
    if (!user || !restoreMasterPassword.trim()) {
      setRestoreError(language === 'zh-CN' ? 'è¯·è¾“å…¥ä¸»å¯†ç ' : 'Please enter master password');
      return;
    }

    setRestoring(true);
    setRestoreError('');

    try {
      // è·å–ç›å€¼ï¼ˆä¼˜å…ˆä» localStorageï¼Œå¦‚æœæ²¡æœ‰åˆ™ä»æœåŠ¡å™¨è·å–ï¼‰
      let salt = crypto.getSalt(user.email);
      if (!salt) {
        // æœ¬åœ°æ²¡æœ‰ç›å€¼ï¼Œä»æœåŠ¡å™¨è·å–
        const saltResponse = await api.getSalt(user.email);
        if (!saltResponse.success || !saltResponse.data) {
          setRestoreError(language === 'zh-CN'
            ? 'æ— æ³•è·å–åŠ å¯†å¯†é’¥ï¼Œè¯·ç¨åé‡è¯•'
            : 'Failed to get encryption key, please try again later');
          setRestoring(false);
          return;
        }
        salt = saltResponse.data.salt;
      }

      // ä½¿ç”¨ä¸»å¯†ç å’Œç›å€¼é‡æ–°æ´¾ç”ŸåŠ å¯†å¯†é’¥
      await crypto.restore(restoreMasterPassword, user.email, salt);

      // éªŒè¯è®¤è¯å“ˆå¸Œæ˜¯å¦æ­£ç¡®ï¼ˆé€šè¿‡å¿ƒè·³æ£€æµ‹éªŒè¯ï¼‰
      const response = await api.heartbeat();
      if (!response.success) {
        throw new Error('Session invalid');
      }

      // æ¢å¤æˆåŠŸ
      setShowSessionRestoreDialog(false);
      setRestoreMasterPassword('');
      setRestoreError('');

      // åŠ è½½æ•°æ®
      await loadNotes();
      await loadPasswords();
    } catch (err: any) {
      console.error('Session restore failed:', err);
      setRestoreError(language === 'zh-CN'
        ? 'ä¸»å¯†ç é”™è¯¯æˆ–ä¼šè¯å·²è¿‡æœŸï¼Œè¯·é‡æ–°ç™»å½•'
        : 'Invalid password or session expired, please login again');
    } finally {
      setRestoring(false);
    }
  };

