/**
 * ç®€åŒ–ç‰ˆ Markdown æ¸²æŸ“å™¨
 * ä¸“ä¸º iOS 16 Safari è®¾è®¡ï¼Œå®Œå…¨ä¸ä¾èµ– ReactMarkdown
 * åªæ”¯æŒåŸºç¡€è¯­æ³•ï¼Œä¿è¯ 100% ç¨³å®šæ€§
 */

import React from 'react';

interface SimpleMarkdownProps {
  content: string;
  className?: string;
}

/**
 * ç®€å•çš„ Markdown è½¬ HTML
 * æ”¯æŒï¼šæ ‡é¢˜ã€æ®µè½ã€åˆ—è¡¨ã€åŠ ç²—ã€æ–œä½“ã€é“¾æ¥ã€ä»£ç 
 */
export function SimpleMarkdownRenderer({ content, className = '' }: SimpleMarkdownProps) {
  const parseMarkdown = (text: string): React.ReactNode[] => {
    const lines = text.split('\n');
    const elements: React.ReactNode[] = [];
    let listItems: string[] = [];
    let orderedListItems: string[] = [];
    let inCodeBlock = false;
    let codeBlockContent: string[] = [];
    let key = 0;

    const flushList = () => {
      if (listItems.length > 0) {
        elements.push(
          <ul key={`ul-${key++}`} className="list-disc list-inside my-4 text-gray-700 dark:text-gray-100 space-y-1">
            {listItems.map((item, idx) => (
              <li key={idx} className="text-base leading-relaxed ml-4" dangerouslySetInnerHTML={{ __html: parseInline(item) }} />
            ))}
          </ul>
        );
        listItems = [];
      }
      if (orderedListItems.length > 0) {
        elements.push(
          <ol key={`ol-${key++}`} className="list-decimal list-inside my-4 text-gray-700 dark:text-gray-100 space-y-1">
            {orderedListItems.map((item, idx) => (
              <li key={idx} className="text-base leading-relaxed ml-4" dangerouslySetInnerHTML={{ __html: parseInline(item) }} />
            ))}
          </ol>
        );
        orderedListItems = [];
      }
    };

    const flushCodeBlock = () => {
      if (codeBlockContent.length > 0) {
        elements.push(
          <pre key={`pre-${key++}`} className="bg-gray-100 dark:bg-gray-800 p-4 rounded-lg border border-gray-300 dark:border-gray-700 my-4 overflow-x-auto">
            <code className="text-sm text-gray-900 dark:text-gray-100 font-mono">
              {codeBlockContent.join('\n')}
            </code>
          </pre>
        );
        codeBlockContent = [];
      }
    };

    lines.forEach((line) => {
      // ä»£ç å—æ£€æµ‹
      if (line.startsWith('```')) {
        if (inCodeBlock) {
          flushCodeBlock();
          inCodeBlock = false;
        } else {
          flushList();
          inCodeBlock = true;
        }
        return;
      }

      if (inCodeBlock) {
        codeBlockContent.push(line);
        return;
      }

      // æ ‡é¢˜
      if (line.startsWith('#')) {
        flushList();
        const match = line.match(/^(#{1,6})\s+(.+)$/);
        if (match) {
          const level = match[1].length;
          const text = match[2];
          const Tag = `h${level}` as keyof JSX.IntrinsicElements;
          const classNames = [
            'text-3xl font-bold text-gray-900 dark:text-white mb-4 mt-2 leading-tight',
            'text-2xl font-bold text-gray-900 dark:text-white mb-4 mt-6 pb-2 border-b border-gray-300 dark:border-gray-700 leading-snug',
            'text-xl font-bold text-gray-900 dark:text-white mb-3 mt-5 leading-snug',
            'text-lg font-bold text-gray-900 dark:text-white mb-2 mt-4 leading-normal',
            'text-base font-bold text-gray-900 dark:text-white mb-2 mt-3 leading-normal',
            'text-sm font-bold text-gray-900 dark:text-white mb-2 mt-2 leading-normal',
          ];
          elements.push(
            <Tag key={`h${level}-${key++}`} className={classNames[level - 1]} dangerouslySetInnerHTML={{ __html: parseInline(text) }} />
          );
        }
        return;
      }

      // æ— åºåˆ—è¡¨
      if (line.match(/^[\*\-\+]\s+/)) {
        orderedListItems = [];
        const text = line.replace(/^[\*\-\+]\s+/, '');
        listItems.push(text);
        return;
      }

      // æœ‰åºåˆ—è¡¨
      if (line.match(/^\d+\.\s+/)) {
        listItems = [];
        const text = line.replace(/^\d+\.\s+/, '');
        orderedListItems.push(text);
        return;
      }

      // åˆ†å‰²çº¿
      if (line.match(/^(\-{3,}|\*{3,}|_{3,})$/)) {
        flushList();
        elements.push(<hr key={`hr-${key++}`} className="border-gray-300 dark:border-gray-600 my-6" />);
        return;
      }

      // å¼•ç”¨
      if (line.startsWith('>')) {
        flushList();
        const text = line.replace(/^>\s*/, '');
        elements.push(
          <blockquote key={`quote-${key++}`} className="border-l-4 border-blue-500 bg-blue-50 dark:bg-blue-500/5 pl-4 pr-4 py-2 my-4 italic text-gray-700 dark:text-gray-300">
            <p dangerouslySetInnerHTML={{ __html: parseInline(text) }} />
          </blockquote>
        );
        return;
      }

      // ç©ºè¡Œ
      if (line.trim() === '') {
        flushList();
        return;
      }

      // æ™®é€šæ®µè½
      flushList();
      elements.push(
        <p key={`p-${key++}`} className="text-base text-gray-700 dark:text-gray-100 mb-3 leading-relaxed" dangerouslySetInnerHTML={{ __html: parseInline(line) }} />
      );
    });

    flushList();
    flushCodeBlock();

    return elements;
  };

  /**
   * è§£æè¡Œå†…å…ƒç´ ï¼šåŠ ç²—ã€æ–œä½“ã€è¡Œå†…ä»£ç ã€é“¾æ¥
   */
  const parseInline = (text: string): string => {
    // è½¬ä¹‰ HTML ç‰¹æ®Šå­—ç¬¦
    let result = text
      .replace(/&/g, '&amp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;');

    // åŠ ç²—ï¼š**text** æˆ– __text__
    result = result.replace(/\*\*(.+?)\*\*/g, '<strong class="font-bold text-gray-900 dark:text-white">$1</strong>');
    result = result.replace(/__(.+?)__/g, '<strong class="font-bold text-gray-900 dark:text-white">$1</strong>');

    // æ–œä½“ï¼š*text* æˆ– _text_
    result = result.replace(/\*(.+?)\*/g, '<em class="italic text-gray-700 dark:text-gray-200">$1</em>');
    result = result.replace(/_(.+?)_/g, '<em class="italic text-gray-700 dark:text-gray-200">$1</em>');

    // è¡Œå†…ä»£ç ï¼š`code`
    result = result.replace(/`(.+?)`/g, '<code class="text-sm text-pink-600 dark:text-pink-400 bg-gray-200 dark:bg-gray-800 px-1.5 py-0.5 rounded font-mono">$1</code>');

    // é“¾æ¥ï¼š[text](url)
    result = result.replace(/\[(.+?)\]\((.+?)\)/g, '<a href="$2" target="_blank" rel="noopener noreferrer" class="text-blue-600 dark:text-blue-400 underline decoration-blue-600/30 dark:decoration-blue-400/30 hover:decoration-blue-600 dark:hover:decoration-blue-400 transition-colors">$1</a>');

    return result;
  };

  const elements = parseMarkdown(content);

  return (
    <div className={`simple-markdown ${className}`}>
      {elements.length > 0 ? elements : (
        <p className="text-base text-gray-500 dark:text-gray-400 italic">
          æš‚æ— å†…å®¹
        </p>
      )}
    </div>
  );
}
