/**
 * Have I Been Pwned (HIBP) å¯†ç æ³„éœ²æ£€æµ‹
 * ä½¿ç”¨ k-åŒ¿åæ€§åè®®ä¿æŠ¤éšç§
 */

/**
 * è®¡ç®— SHA-1 å“ˆå¸Œ
 */
async function sha1(message: string): Promise<string> {
  const msgBuffer = new TextEncoder().encode(message);
  const hashBuffer = await crypto.subtle.digest('SHA-1', msgBuffer);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  return hashHex.toUpperCase();
}

/**
 * æ£€æŸ¥å¯†ç æ˜¯å¦åœ¨æ³„éœ²æ•°æ®åº“ä¸­
 *
 * @param password - è¦æ£€æŸ¥çš„å¯†ç 
 * @returns æ³„éœ²æ¬¡æ•°ï¼ˆ0 è¡¨ç¤ºæœªæ³„éœ²ï¼‰
 */
export async function checkPasswordBreach(password: string): Promise<number> {
  try {
    // 1. è®¡ç®—å¯†ç çš„ SHA-1 å“ˆå¸Œ
    const hash = await sha1(password);

    // 2. ä»…å‘é€å“ˆå¸Œçš„å‰ 5 ä½ï¼ˆk-åŒ¿åæ€§åè®®ï¼‰
    const prefix = hash.substring(0, 5);
    const suffix = hash.substring(5);

    // 3. è¯·æ±‚ HIBP APIï¼ˆç§»é™¤ User-Agent å¤´ä»¥é¿å… CORS é”™è¯¯ï¼‰
    const response = await fetch(`https://api.pwnedpasswords.com/range/${prefix}`, {
      method: 'GET',
    });

    if (!response.ok) {
      throw new Error(`HIBP API error: ${response.status}`);
    }

    // 4. è§£æå“åº”ï¼ˆæ ¼å¼ï¼šåç¼€:æ¬¡æ•°\r\nåç¼€:æ¬¡æ•°\r\n...ï¼‰
    const text = await response.text();
    const lines = text.split('\r\n');

    // 5. åœ¨è¿”å›çš„å“ˆå¸Œåˆ—è¡¨ä¸­æŸ¥æ‰¾åŒ¹é…é¡¹
    for (const line of lines) {
      const [hashSuffix, count] = line.split(':');
      if (hashSuffix === suffix) {
        return parseInt(count, 10);
      }
    }

    // 6. æœªæ‰¾åˆ°åŒ¹é…ï¼Œè¯´æ˜æœªæ³„éœ²
    return 0;
  } catch (error) {
    console.error('Password breach check failed:', error);
    throw error;
  }
}

/**
 * å¯†ç æ³„éœ²æ£€æµ‹ç»“æœç±»å‹
 */
export interface BreachCheckResult {
  breached: boolean;        // æ˜¯å¦æ³„éœ²
  count: number;            // æ³„éœ²æ¬¡æ•°
  severity: 'safe' | 'low' | 'medium' | 'high' | 'critical'; // ä¸¥é‡ç¨‹åº¦
}

/**
 * æ£€æŸ¥å¯†ç æ³„éœ²å¹¶è¿”å›è¯¦ç»†ç»“æœ
 */
export async function checkPasswordBreachWithDetails(password: string): Promise<BreachCheckResult> {
  const count = await checkPasswordBreach(password);

  let severity: BreachCheckResult['severity'];
  if (count === 0) {
    severity = 'safe';
  } else if (count < 10) {
    severity = 'low';
  } else if (count < 100) {
    severity = 'medium';
  } else if (count < 10000) {
    severity = 'high';
  } else {
    severity = 'critical';
  }

  return {
    breached: count > 0,
    count,
    severity,
  };
}

/**
 * ç®€å•çš„ç¼“å­˜æœºåˆ¶ï¼ˆé¿å…é‡å¤æ£€æŸ¥åŒä¸€å¯†ç ï¼‰
 */
const breachCheckCache = new Map<string, { result: BreachCheckResult; timestamp: number }>();
const CACHE_TTL = 7 * 24 * 60 * 60 * 1000; // 7 å¤©

/**
 * å¸¦ç¼“å­˜çš„å¯†ç æ³„éœ²æ£€æµ‹
 */
export async function checkPasswordBreachCached(password: string): Promise<BreachCheckResult> {
  // 1. è®¡ç®—å¯†ç å“ˆå¸Œä½œä¸ºç¼“å­˜ keyï¼ˆä¸å­˜å‚¨æ˜æ–‡å¯†ç ï¼‰
  const cacheKey = await sha1(password);

  // 2. æ£€æŸ¥ç¼“å­˜
  const cached = breachCheckCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < CACHE_TTL) {
    return cached.result;
  }

  // 3. æ‰§è¡Œæ£€æŸ¥
  const result = await checkPasswordBreachWithDetails(password);

  // 4. å­˜å…¥ç¼“å­˜
  breachCheckCache.set(cacheKey, {
    result,
    timestamp: Date.now(),
  });

  return result;
}

/**
 * æ¸…é™¤ç¼“å­˜ï¼ˆç”¨äºæµ‹è¯•æˆ–æ‰‹åŠ¨åˆ·æ–°ï¼‰
 */
export function clearBreachCheckCache() {
  breachCheckCache.clear();
}
